<template>
	<div class="">
		<div v-if="addShow" class="add-category ">

			<div class="order-back">
				<el-button type="primary" size="small" @click="back"></el-button>
			</div>
			<order-details :order_id='this.order_id'></order-details>
		</div>

		<template v-if="!addShow">
			<el-table :data="list" border style="width: 100%">
				<el-table-column type="index" width="50"> </el-table-column>
				<el-table-column prop="order_num" label="" width="200"></el-table-column>
				<el-table-column prop="because" label="" width="160"></el-table-column>
				<el-table-column prop="message" label="" width="220"> </el-table-column>
				<el-table-column prop="ip" label="IP" width="180"></el-table-column>
				<el-table-column prop="money" label="" width="180"></el-table-column>
				<el-table-column prop="operation" label="">
					<template slot-scope="scope">
						<el-button type="primary" size="small" v-if="scope.row.status==0" @click="sub(scope.row.order_id,scope.$index)"></el-button>
						<el-button style="margin-left: 10px" type="danger" size="small" slot="reference" v-if="scope.row.status==0"
						 @click="bohui(scope.row.id,scope.$index)"></el-button>
						<span v-if="scope.row.status==1">-{{scope.row.wx_id}}</span>
						<span v-if="scope.row.status==-1">-{{scope.row.remark}}</span>
					</template>
				</el-table-column>
			</el-table>
		</template>
	</div>
</template>

<script>
	import OrderDetails from '../../Details.vue'
	export default {
		name: 'Customer-b',
		data() {
			return {
				visible2: false,
				list: [],
				addShow: false, //false 
				checkdDistributor: null,
				order_id: 0
			}
		},
		components: {
			OrderDetails,
		},
		methods: {
			get_list() {
				var that = this;
				this.http.post("/admin/get_tui_all").then((res) => {
					that.list = res.data;
				})
			},
			//
			sub(id, index) {
				var that = this;
				this.$confirm('?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					this.http.post('/admin/tui_money', {
						id: id
					}).then(res => {
						that.$message({
							showClose: true,
							message: '',
							type: 'success'
						});
						this.get_list();
					});
				})
			},
			//
			bohui(id, index) {
				var that = this;
				this.$prompt('', '', {
						confirmButtonText: '',
						cancelButtonText: ''
					}).then(({ value }) => {
								this.http.post('/admin/tui_bohui', {
									id: id,
									msg: value
								}).then(res => {
									console.log(res)
									this.get_list();
								})
							})
						}
					},
					//vue
					mounted() {
						this.get_list();
					}



			}
</script>

<style lang="less" scoped="">
	.order-back {
		margin-bottom: 10px;
	}
</style>
